@media screen {
  /* 
  For debugging the baseline grid append ?grid to the url 
  */
  .grid .pagedjs_page_content {
    background-image: repeating-linear-gradient(to bottom, transparent 0px, transparent 11.25pt, rgba(255, 0, 0, 0.1) 11.25pt, rgba(255, 0, 0, 0.1) 12pt);
  }
}
h1 {
  page-break-before: left;
}

h2 {
  page-break-before: right;
}

[class^=toclimit-] {
  page-break-before: always;
}

.chapter {
  page-break-before: right;
}

.chapter.chapter-introduction {
  page-break-before: left !important;
}

.author {
  page-break-after: always;
}

/* General styling */
html {
  font-size: 62.5%;
}

body {
  color: var(--color-main);
}

img {
  max-width: 100%;
  height: auto;
}

/*
Setting named-pages
https://pagedjs.org/documentation/8-named-page/
*/
#introduction,
.chapter-introduction {
  page: intro;
}

#cover {
  page: toc;
  list-style: none;
}

/* Prevent superscript from messing up the line-height */
sup {
  vertical-align: top;
  position: relative;
  top: -0.5em;
}

a {
  text-decoration: none;
  color: black;
}

/* Generic css */
/* Show url of a tags */
/* a::after {
content: " (" attr(href) ")";
} */
img {
  max-width: 100%;
}

/* 
Page margin colors. The color names are specified in sketch.js
Available color: red, green, brown
*/
@page toc {
  --base-color: green;
}
@page intro {
  --base-color: brown;
}
.pagedjs_intro_page + .pagedjs_blank_page {
  --base-color: brown;
}

.pagedjs_intro_page + div {
  --base-color: brown;
}

/* 
	Change the display of these margin elements to inline-block
	so they have a meaningful width for use with the P5js sketch.
*/
.pagedjs_pages .pagedjs_margin-top-center .pagedjs_margin-content,
.pagedjs_pages .pagedjs_margin-bottom-center .pagedjs_margin-content {
  display: inline-block;
  width: auto !important;
  margin: 0 auto;
}

/* For placement op p5js sketch relative to the page */
.pagedjs_page, .pagedjs_pagebox {
  position: relative;
}

/* First page */
.book-title {
  font-family: Ortica, serif;
  font-weight: 300;
  font-size: 14pt;
  line-height: 15pt;
}