/* Fonts & typography & baseline grid 
the baseline grid is tightly coupled (by Plumber) to 
the typeface so you find them combined in this file.
*/

// @import "font-literata";
@import "font-ortica";
@import "font-combine";
@import "font-authentic";

body {
	@include font-ortica();
	@include plumber(
      $baseline: $baseline-ortica,
			$font-size: pt(9.5pt),
			$line-height: 1
	);
}

h1 {
	@include font-combine();
	// font-size: 24pt;
	@include plumber(
			$leading-top: 0,
			$leading-bottom: 0,
      $baseline: $baseline-combine,
			$font-size: pt(24pt),
	);
}

.pagedjs_pages {
  .chapter > p {
		@include plumber(
			$baseline: $baseline-ortica,
			$font-size: pt(9.5pt),
		);
		orphans: 5;
		widows: 5;
	}
	b {
		@include plumber(
			$baseline: $baseline-ortica,
			$font-size: pt(9.5pt),
			$leading-top: 0,
			$leading-bottom: 0,
		);
		padding: 0;
		margin: 0;
	}
	.note {
    @include plumber(
			$leading-top: 0,
			$leading-bottom: 0,
      $baseline: $baseline-ortica,
      $font-size: pt(9.5pt),
    );
  }
	.chapter {
		h2 {
			@include font-combine();
			@include plumber(
				$baseline: $baseline-authentic,
				$font-size: pt(24pt),
				$line-height: 3
			);
			text-align: center;
		}
		.author {
			@include font-authentic(90);
			@include plumber(
				$baseline: $baseline-authentic,
				$font-size: pt(7pt),
				$leading-top: 0,
				$leading-bottom: 0,
			);
			display: block;
			text-align: center;
		}
		blockquote {
			@include font-combine;
			@include plumber(
				$baseline: $baseline-combine,
				$font-size: pt(8.5pt),
			);
			> p {
				margin: 0;
			}
		}
		ul,
		ol,
		dl {
			@include plumber(
				$baseline: $baseline-ortica,
				$font-size: pt(9.5pt),
			);
		}
		sup,
		.footnote {
			@include font-authentic($weight:150);
			@include plumber(
				$baseline: $baseline-authentic,
				$font-size: pt(7pt),
				$leading-top: 0,
				$leading-bottom: 0,
			);
			vertical-align: top; 
			position: relative; 
			top: -0.2em;
			&:after {
				font-variant-position: initial;
			}
			// font-size: 7pt;
			// line-height: 8.8pt;
		}		
		
	}

	/* we have to disable the baseline grid margin/padding 
	that Plumber sets above for the first element on a page */
	.chapter > *:first-child {
		margin-top: 0;
		padding-top: 0;
	}

// ul, li {
// 	@include plumber(
// 		$line-height: 3,
// 		$leading-top: 0,
// 		$leading-bottom: 0
//     // $baseline: 0.065,
//     // $font-size: 1,
//   )
// }
}

#toc {
	ul {
		list-style: none;

	}

	& > ul > li {
		& > a .toctext { /* first level */
			@include font-ortica();
			@include plumber(
				$baseline: $baseline-ortica,
				$font-size: pt(7pt),
				$leading-top: 0,
				$leading-bottom: 0,
			);
		}
		& > ul {
			li .toctext {
				// padding-left: 1cm;
				@include font-authentic(90);
				@include plumber(
					$baseline: $baseline-authentic,
					$font-size: pt(7pt),//0.58333333, // 7pt
					$leading-top: 0,
					$leading-bottom: 0,
				);
			}
		} 
	}
	.tocnumber {
		display: none;
	}
	.tocauthor {
		@include font-authentic(90);
				@include plumber(
					$baseline: $baseline-authentic,
					$font-size: pt(11pt),
				);
	}
}

.pagedjs_margin {
	@include font-authentic($weight:130);
	font-size: 7pt;
	text-align: center;
}

span.footnote {
  @include font-authentic($weight:90);
	font-size: 7pt;
	line-height: 8.8pt;
}


